import * as React from 'react';
import {BottomNavigation, Text} from 'react-native-paper';
import PeopleList from './person/peoplelist.js';

const MainPage = () => {
  const ClientsRoute = () => <PeopleList />;
  const AlertasRoute = () => <Text>Alertas</Text>;
  const MenuRoute = () => <Text>Menu</Text>;
  const [index, setIndex] = React.useState(0);
  const [routes] = React.useState([
    {key: 'clientes', title: 'Clientes', focusedIcon: 'account-multiple', unfocusedIcon: 'account-multiple'},
    {key: 'alertas', title: 'Alertas', focusedIcon: 'album', unfocusedIcon: 'album'},
    {key: 'menu', title: 'Menu', focusedIcon: 'history', unfocusedIcon: 'history'},
  ]);

  const renderScene = BottomNavigation.SceneMap({
    clientes: ClientsRoute,
    alertas: AlertasRoute,
    menu: MenuRoute,
  });

  return (
    <BottomNavigation
      navigationState={{index, routes}}
      onIndexChange={setIndex}
      renderScene={renderScene}
    />
  );
};

export default MainPage;
