import * as React from 'react';
import {Text} from 'react-native-paper';
import {ScrollView, View} from 'react-native';
import {connect} from 'react-redux';
import styles from '../styles.js';
import Person from './person.js';
import PersonCard from './personcard.js';
import SearchBar from '../searchonlist.js';
import {FAB, Dialog, Portal, Button, Paragraph} from 'react-native-paper';
import {setPeopleFilter, removePerson} from '../../actions';

class PeopleList extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      id: null,
      deleteDialog: false,
      deleteId: null,
      deleteName: '',
    };
    this.handleClick = this.handleClick.bind(this);
    this.deletePerson = this.deletePerson.bind(this);
    this.showDeleteDialog = this.showDeleteDialog.bind(this);
    this.hideDeleteDialog = this.hideDeleteDialog.bind(this);
    this.updateDeletePersonCard = this.updateDeletePersonCard.bind(this);
  }

  componentDidMount() {
    this.setState({
      id: this.props.id,
      deleteDialog: false,
      deleteId: null,
    });
  }
  componentDidUpdate() {
	//this.updateDeletePersonCard()
  } 
  componentWillUnmount() {   
  }
  hideDeleteDialog = () => {
    this.setState({ deleteDialog: false });
  }
  showDeleteDialog = () => {
    this.setState({ deleteDialog: true });
  }
  ClientRoute = (id) => (<Person id={id} goback={this.handleClick}/>);
  
  handleClick = (idvalue) => {
    this.setState({ id: idvalue });
  }
  
  deletePerson = (idvalue) => {
	this.setState({ deleteId: idvalue });
	this.updateDeletePersonCard(idvalue);
	this.showDeleteDialog();
//    this.props.removePerson(idvalue);
  }
  
   updateDeletePersonCard = (idValue) => {
	var item = this.props.lista.filter(e => e.id == idValue);
	if (item.length == 1) 
		this.setState({ deleteName: item[0].name });
  }

  renderRow = (row) => {
	    return (
	    	    <View style={styles.cardTopMargin} key={row.id}>
			    <PersonCard 
			    	name={row.name} 
			    	cpf={row.cpf} 
			    	key={row.id} 
			    	id={row.id} 
			    	click={this.handleClick.bind(this, row.id)}
			    	deletePerson={this.deletePerson.bind(this, row.id)}>
			    </PersonCard>
		    </View>
	    )
  }
  
  fab =	<FAB style={styles.fab} small icon="account-plus" onPress={() => this.handleClick(0)} />;

  render() {

    return (
    
	<>
		{this.state.id == null ? <SearchBar searchValue={this.props.searchFilter} searchQuery={this.props.setPeopleFilter}/> : null}
		{this.state.id == null ? null : this.ClientRoute(this.state.id)}
    		{ this.props.length == 0 ? 
    		
	    		<Text>Nenhum item</Text> :
	    
			<ScrollView
			  contentInsetAdjustmentBehavior="automatic"
			   style={styles.scrollView}>

					{this.state.id == null ? this.props.lista.map(this.renderRow) : null}
			</ScrollView>

		}
        	{ this.state.id == null ? this.fab : null}
		<Portal>
			<Dialog visible={this.state.deleteDialog} onDismiss={this.hideDeleteDialog}>
				<Dialog.Title>Você quer deletar?</Dialog.Title>
				<Dialog.Content>
					<Paragraph>{this.state.deleteName} </Paragraph>
				</Dialog.Content>
				<Dialog.Actions>
					<Button onPress={this.hideDeleteDialog}>Cancelar</Button>
					<Button onPress={() => { this.hideDeleteDialog(); this.props.removePerson(this.state.deleteId);} }>Deletar</Button>
				</Dialog.Actions>
			</Dialog>
		</Portal>
	</>
    )
  }
}

function  orderAlphabetically(peopleList) {
	return peopleList.sort(function(a, b){
	    if(a.name < b.name) { return -1; }
	    if(a.name > b.name) { return 1; }
	    return 0;
	})
}

export const getPeopleBySearchFilter = (lista, SearchFilter) => {

	var l = orderAlphabetically(lista);
	if (SearchFilter && SearchFilter != '') {
		const regex = new RegExp(SearchFilter, 'i');
		l = lista.filter(person => (regex).test(person.name) || (regex).test(person.cpf)
					|| (regex).test(person.birthdate) || (regex).test(person.avatar))
	}
	return l;
}

const mapStateToProps = (store) => {

	var filteredList = getPeopleBySearchFilter(store.PeopleState.people, store.PeopleState.filter)
	return { lista: filteredList , searchFilter: store.PeopleState.filter}
}

export default connect(mapStateToProps, { setPeopleFilter, removePerson })(PeopleList);
