export const updatePerson = person => ({
  type: 'people/UPDATE_PERSON',
  payload: person,
});

export const addPerson = person => ({
  type: 'people/ADD_PERSON',
  payload: person,
});

export const removePerson = id => ({
  type: 'people/REMOVE_PERSON',
  payload: id,
});

export const setPeopleFilter = searchText => ({
  type: 'people/SET_PEOPLE_FILTER',
  payload: searchText,
});
