import * as React from 'react';
import { Avatar, TextInput, Button } from 'react-native-paper';
import { connect, useDispatch } from 'react-redux'
import { updatePerson, addPerson } from '../../actions'

import {
  SafeAreaView,
  ScrollView,
  View,
  Text,
} from 'react-native';
import Details from '../details.js'
import styles from '../styles.js'

class Person extends React.Component {

	constructor (props) {
		super(props);
		this.state =
		{
			id: '',
			name: '',
			avatar: '',
			birthdate: '',
			cpf: '',
			goback: '',
			updatePerson: '',
			addPerson: ''

		};
		this.handleChangeCpf = this.handleChangeCpf.bind(this);
		this.handleChangeName = this.handleChangeName.bind(this);
		this.handleChangeAvatar = this.handleChangeAvatar.bind(this);
		this.handleChangeBirth = this.handleChangeBirth.bind(this);
		
	}
	
	componentDidMount() {
	
		this.setState( {
			id: this.props.id,
			name: this.props.name,
			avatar: this.props.avatar,
			birthdate: this.props.birthdate,
			cpf: this.props.cpf,
			goback: this.props.goback,
			updatePerson: this.props.updatePerson,
			addPerson: this.props.addPerson

		});
	}
	
	componentDidUpdate() {

	}


	getPerson = () => {
		return {
			id: this.state.id,
			name: this.state.name,
			avatar: this.state.avatar,
			birthdate: this.state.birthdate,
			cpf: this.state.cpf
		}
	}

	handleChangeCpf = (text) => {
		this.setState({ cpf: text })
	}
	handleChangeName = (text) => {
		this.setState({ name: text })
	}
	handleChangeAvatar = (text) => {
		this.setState({ avatar: text })
	}
	handleChangeBirth = (text) => {
		this.setState({ birthdate: text })
	}



  render () {


    return (


		<View >
		  <Details title="Meu Cadastro" goback={this.state.goback}></Details>
		  

			<ScrollView
			  contentInsetAdjustmentBehavior="automatic"
			   style={styles.scrollView}>


		            <View style={styles.body}>
		
		                        <View style={[styles.center, styles.sectionContainer]}>

					 <Avatar.Icon size={100} icon="account" style={{color: 'blue'}} />
			    		</View>		            
		                        <View style={styles.sectionContainer}>
					  <TextInput style={styles.textInput}
					    label="Nome"
					    onChangeText={this.handleChangeName}
					    value={this.state.name}
					  />
					  <TextInput style={styles.textInput}
  					    label="Foto"
					    onChangeText={this.handleChangeAvatar}
					    value={this.state.avatar}
					  />
					  <TextInput style={styles.textInput}
  					    label="Data Nascimento"
					    onChangeText={this.handleChangeBirth}
					    value={this.state.birthdate}
					  />
					  <TextInput style={styles.textInput}
  					    label="CPF"
					    onChangeText={this.handleChangeCpf}
					    value={this.state.cpf}
					  />
			    		</View>

			    			    

		                        <View style={styles.sectionContainer}>
		                          
						  <Button
						  	mode="contained"
							  onPress={()=> this.state.id == 0 ? 
							  			this.state.addPerson(this.getPerson()) : 											this.state.updatePerson(this.getPerson())}> Salvar
						  </Button>
			    		</View>
			    		
			    	        <View style={styles.bottomMargin}>
		                          
			    		</View>
    			    </View>
			</ScrollView>

		</View>

      )
    }
 }
 

const getPersonById = (store, id) => {
	var p = {}
	p = store.PeopleState.people.find(e => e.id == id);
	return p;
}

const mapStateToProps = (store, ownProps) => {
	var p = {};
	if (ownProps.id > 0)
		p = getPersonById(store, ownProps.id);
	p.goback = ownProps.goback;
	return p;
 }

export default connect(mapStateToProps, { updatePerson, addPerson })(Person);

